body {
  margin: 0;
  font-family: "Noto Sans Sinhala", system-ui;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
  font-variation-settings: "wdth" 100;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ddd;
}

p {
  margin-top: 0;
}

.container {
    max-width: 850px;
    margin: 0 auto;
    padding: 30px;
    background-color: #fff;

}

h2 {
  font-size: 22px;
  color: #0053bd;
}

.poem-wrap {
  margin-bottom: 60px;
}